header{
  display: flex;
  height: 60px;
  justify-content: space-around;
  align-items: center;
  border-bottom: 2px solid red;
  border-radius: 5px;
  box-shadow: 2px 2px 4px grey;
  margin-bottom: 5px;
  position: sticky;
  top: 0;
  background-color: white;
}
.Right_side {
 display: flex;
 align-items: center;
}
.Right_side img{
  width: 100px;
  height: 50px;
  box-sizing: border-box;
  align-items: center;
}
.Right_side p{
  font-weight: 600;
}
.Middle_section{
  width: 320px;
  padding-left: 40px;
}
.Middle_section input{
  width: 320px;
  height: 20px;
  border-radius: 5px;
  border: 2px solid black;
}
.left_section img{
  width: 50px;
  height: 50px;
  box-sizing: border-box;
  align-items: center;
  padding: 10px;
}
.mainSection{
  display: flex;
  align-items: flex-start;
}
.mainSection1{
  display: flex;
  flex-direction: column;
  position: sticky;
  top: 65px;
  padding: 15px;
  margin-right: 10px;
  box-shadow: 2px 2px 3px grey;
}

.mainSection2{
   display: flex;
  flex-wrap: wrap;
}
.subMainSection1 img{
  width: 50px;
  padding-left: 5px;
  margin: 15px 0;
}
.subMainSection1 img:hover{
  box-shadow: 2px 2px 3px gray,-2px -2px 3px gray;
  cursor: pointer;
}
.subMainSection1 p{
  font-size: 12px;
  text-align: center;
}
.card{
  width: 31%;
  margin-right: 20px;
  margin-bottom: 20px;
  box-shadow: 2px 2px 3px grey,-2px -2px 3px grey;
  cursor: pointer;
}
.card img{
  width: 100%;
  border-radius: 5px;
  overflow: hidden;
}

.Logo{
  width: 15%;
  padding-top: 15px;
}
.Logo img{
  border-radius: 50%;
}
.subCard{
  display: flex;
}
.subThumbnail{
  display: flex;
  margin: -20px;
  margin-left: 2px;
}
.Descrip{
  display: flex;
}
.nameThumbnail{
  width: 290px;
  padding-left: 15px;
}
